import React from "react"
import { Link, graphql } from "gatsby"
import css from "../css/masseuses.module.scss"
import Img from "gatsby-image"
import Layout from "../components/layout"
import SEO from "../components/seo"

const MasseusesPage = ({
  data: {
    allMasseusesJson: { edges },
  },
}) => {
  console.log(edges)
  return (
    <Layout>
      <SEO title="Masseuses" />
      <div className="container">
        <h1 className="main__heading">Masseuses</h1>
        <ul className={css.masseusesWrapper}>
          {edges.map(({ node: masseuse }) => {
            return (
              <li key={masseuse.key}>
                <Link to={masseuse.fields.slug}>
                  <Img
                    alt={masseuse.name}
                    fluid={masseuse.image.childImageSharp.fluid}
                  />
                </Link>
                <div>
                  <p>
                    <span>Name:</span> {masseuse.name}
                  </p>
                  <p>
                    <span>Age:</span> {masseuse.age}
                  </p>
                  <p>
                    <span>Ethnicty:</span> {masseuse.ethnicity}
                  </p>
                  <p>
                    <span>Hair Color:</span> {masseuse.hair_color}
                  </p>
                  <p>
                    <span>Height:</span> {masseuse.height}
                  </p>
                  <p>
                    <span>Weight:</span> {masseuse.weight}
                  </p>
                  <p>
                    <span>Measurements:</span> {masseuse.measurements}
                  </p>
                </div>
                <div>
                  <a href="tel:+19492326088">Call Now</a>
                  <Link to={masseuse.fields.slug}>More Info</Link>
                </div>
              </li>
            )
          })}
        </ul>
      </div>
    </Layout>
  )
}

export const masseusesPageQuery = graphql`
  query masseusesPageQuery {
    allMasseusesJson {
      edges {
        node {
          fields {
            slug
          }
          key
          name
          age
          height
          weight
          hair_color
          ethnicity
          measurements
          image {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid_tracedSVG
              }
            }
          }
        }
      }
    }
  }
`

export default MasseusesPage
